import React from 'react'
import Heading from './components/Heading'
import { Box, Button, Container, FormControl, IconButton, ListItemText, MenuItem, Select, Typography } from '@mui/material'
import FormatListBulletedIcon from "@mui/icons-material/FormatListBulleted";
import GridViewIcon from "@mui/icons-material/GridView";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import PropertyListing from './components/PropertyListing'

export default function PropertyListFilterIndex() {
    const [sort, setSort] = useState("Relevance");
    const handleSortChange = (event) => {
        setSort(event.target.value);
      };
      const menuOptions = [
        { label: "Relevance", value: "Relevance" },
        { label: "Price - Low to High", value: "PriceLowToHigh" },
        { label: "Price - High to Low", value: "PriceHighToLow" },
        { label: "Most Recent", value: "MostRecent" },
        { label: "Rate/sqft - Low to High", value: "RatePerSqftLowToHigh" },
        { label: "Rate/sqft - High to Low", value: "RatePerSqftHighToLow" },
      ];
    
      const menuItems = menuOptions.flatMap((option, index) => [
        <MenuItem
          key={option.value}
          value={option.value}
          sx={{
            fontWeight: sort === option.value ? 600 : "normal",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          <ListItemText sx={{ fontWeight: sort === option.value ? 600 : "normal" }}>
            {option.label}
          </ListItemText>
          {sort === option.value && (
            <CheckCircleOutlineIcon fontSize="small" color="success" />
          )}
        </MenuItem>,
        index < menuOptions.length - 1 && <Divider key={`divider-${index}`} />,
      ]);
  return (
   <Box sx={{backgroundColor: "#F7F7F7"}}>
     <Container maxWidth="xl" sx={{ px: { lg: 7, xs: 1 } }} >
      <Heading/>
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          mb: 2,
          // border: "2px solid #D8D8D8",
          borderRadius: "8px",
          px: 4,
          py: 2,
          background: "#fff",
          flexWrap: "wrap",
          gap: 2,
        }}
      >
        {/* <Typography variant="body1">
          Showing 1 â€“ {properties.length} of {properties.length} results
        </Typography> */}
        <Box
          sx={{
            display: "flex",
            gap: 2,
            alignItems: "center",
            flexWrap: "wrap",
          }}
        >
          <Box sx={{ display: "flex", gap: 2 }}>
            <IconButton
              sx={{
                borderRadius: "8px",
                border: "2px solid #D8D8D8",
                color: "#4D4D4D",
                "&:hover": {
                  background: "#E3E3E3",
                  color: "#4D4D4D",
                },
                height: "42px",
                width: "42px",
              }}
            >
              <GridViewIcon />
            </IconButton>
            <IconButton
              sx={{
                borderRadius: "8px",
                border: "2px solid #D8D8D8",
                color: "#4D4D4D",
                "&:hover": {
                  background: "#E3E3E3",
                  color: "#4D4D4D",
                },
                height: "42px",
                width: "42px",
              }}
            >
              <FormatListBulletedIcon />
            </IconButton>
            <Button
              startIcon={<FavoriteBorderIcon />}
              sx={{
                textTransform: "none",
                borderRadius: "8px",
                color: "#4D4D4D",
                "&:hover": {
                  background: "#E3E3E3",
                  color: "#4D4D4D",
                },
                height: "42px",
              }}
            >
              Save Search
            </Button>
          </Box>
          <Typography>Sort By</Typography>
          <FormControl
            variant="outlined"
            size="small"
            sx={{
              "& .MuiOutlinedInput-notchedOutline": {
                border: "none", // No border around the field
              },
              "& .MuiOutlinedInput-root": {
                paddingRight: "8px", // Adjust padding for the dropdown icon
              },
              "& .MuiSelect-icon": {
                top: "calc(50% - 12px)", // Center align the arrow icon
                right: "8px", // Align icon to the right
              },
              "& .Mui-focused .MuiOutlinedInput-notchedOutline": {
                border: "none", // No border on focus
              },
            }}
          >
            <Select
              value={sort}
              onChange={handleSortChange}
              displayEmpty
              renderValue={(selected) => {
                const selectedOption = menuOptions.find(
                  (option) => option.value === selected
                );
                return selectedOption ? selectedOption.label : "";
              }}
              MenuProps={{
                PaperProps: {
                  sx: {
                    borderRadius: "8px", // Rounded corners for dropdown menu
                    boxShadow: 5, // Subtle shadow
                    marginTop: "30px",
                    "& .MuiMenuItem-root": {
                      padding: "12px 16px",
                    },
                    "& .MuiDivider-root": {
                      margin: "0 16px",
                    },
                  },
                },
              }}
              sx={{
                "& .MuiSelect-select": {
                  padding: "8px 16px",
                  fontSize: "14px",
                  display: "flex",
                  alignItems: "center",
                },
              }}
            >
              {menuItems}
            </Select>
          </FormControl>
        </Box>
      </Box>
      <PropertyListing/>
    </Container>
   </Box>
  )
}
